* {
  margin: 0;
  padding: 0px;
}

body {
  box-sizing: border-box;
  font-family: "Lato", sans-serif;

  /* scrollbar */
  overflow-x: hidden;
  overflow-y: scroll;
}

.text-primary-p {
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
}

.font-bold {
  font-weight: 700;
}

.text-title {
  color: #ffffff;
}

.text1 {
  color: #ffffff;
}

.container {
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas:
    "sidebar nav nav nav"
    "sidebar main main main";
}

.navbar {
  background: #ffffff;
  grid-area: nav;
  height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 30px 0 30px;
  border-bottom: 1px solid lightgray;
}

.button {
  width: 30px;
  height: 40px;
  background-color: green;
}

